@import url(https://fonts.googleapis.com/css2?family=Rock+Salt&display=swap);
@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@300&family=Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fredericka+the+Great&display=swap);



.primaryNav > ul > li > a {
    color: white;
    text-decoration: none;
}

.subheader1, .siteTitle {
    text-align:center;
}

h1, h2 {
    font-family: "Rock Salt";
}

h1 {
    font-size: 1.2rem;
}

h2 {
    font-size: 1rem;
}

.primaryNav > ul > li:hover {
    box-shadow: 0px 0px 15px -1px #720404
}

.subheader1, .subheader2 {
    display:none;
}



.mainHeader {
    width: 100%;
    margin: 0;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    padding-bottom:1rem;
    max-height:20rem;
    background-color: var(--reb-mute);
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 0.75fr 1fr;
    grid-gap: 0px 0px;
    gap: 0px 0px;
    grid-template-areas:
      "logoImage siteTitle siteTitle"
      "primaryNav primaryNav primaryNav";
}

.logoImage {
    margin-top: 1.75rem;
    padding: 0.5rem;
    max-width: 120px;
    grid-area: logoImage;
}

.primaryNav {
    grid-area: primaryNav;
}

.siteTitle {
    padding:0.25rem;
    grid-area: siteTitle;
}

.primaryNav > ul{
    list-style-type: none;
    text-align: center;
    -webkit-padding-start: 0;
            padding-inline-start: 0;
}

.primaryNav > ul > li {
    display: inline-block;
    font-size: 1.75rem;
    font-weight: bold;
    color: white;
    padding: 0.5rem 1rem;
    margin: 0 0.5rem 1rem 0.5rem;
    border-radius: 10px;
    background-color: var(--reb);
}

#break {
    display: block;
}

@media screen and (min-width: 576px) and (max-width: 810px) {

    h1 {
        font-size: 1.5rem;
    }

    .mainHeader {
        max-height: 15rem;
    }

    #break {
        display: none;
    }
}


@media screen and (min-width: 810px) {
    .primaryNav > ul > li { 
        font-size: 2rem;
    }

    .mainHeader { 
        box-shadow: 1px 3px 19px 1px rgba(0,0,0,0.92);
    }
}


@media screen and (min-width: 810px) and (max-width: 1600px) {

    .logoImage {
        margin-top: 0;
        max-width: 250px;
    }


    .mainHeader {
        width: 97%;
        margin: 0 auto;
        border-radius: 10px;
        padding: 1rem;
        height: 12.5rem;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        grid-template-rows: 1fr 1fr;
        grid-gap: 0px 0px;
        gap: 0px 0px;
        grid-template-areas:
          "logo siteTitle primaryNav"
          "logo subheader1 primaryNav";
    }

    .siteTitle {
        text-align: center;
        grid-area: siteTitle; 
    }

        .logo {
            grid-area: logo;
        }

    .primaryNav {
        grid-area: primaryNav; 
    }

    
    .subheader1 {display: block; text-align: center; grid-area: subheader1; }
    .subheader2 {display: block; grid-area: subheader2; }



}



@media screen and (min-width: 1600px) {
    .mainHeader {
        width: 97%;
        margin: 0 auto;
        border-radius: 10px;
        padding: 1rem;
        height:9rem;
        display: grid;
        grid-template-columns: 0.8fr 1fr 1fr 0.8fr 0.8fr;
        grid-template-rows: 1fr 1fr 1fr;
        grid-gap: 0px 0px;
        gap: 0px 0px;
        grid-template-areas:
          "logo siteTitle siteTitle . ."
          "logo subheader1 subheader1 primaryNav primaryNav"
          "logo subheader2 subheader2 . .";
    }

    .siteTitle {
        text-align: center;
        grid-area: siteTitle; 
    }

    .logo {
        /* float: left; */
        max-height: 12.5rem;
        grid-area: logo;
    }

    .logoImage {
        max-width: 150px;
    }

    h1 {
        margin: 0 !important;
    }

    .logo > img {
        max-height: 12.5rem;
    }

    .primaryNav {
        grid-area: primaryNav; 
    }

    
    .subheader1 {display: block; text-align: center; grid-area: subheader1; }
    .subheader2 {display: block; grid-area: subheader2; }

    .primaryNav > ul > li {
        display:inline;
        font-size: 2rem;
        font-weight: bold;
        padding: 0.5rem 1rem;
        margin: 0 0.5rem;
    }




    .logoImage {
        margin-top: 0;
    }

    #break {
        display: none;
    }


}
.mainFooter {
    text-align:center;
    position: fixed;
    left: 0%;
    bottom: 0;
    width: 100%;
    margin: 0 auto;
    padding: 0.5rem;
    background-color: var(--reb-mute);
}

.footerBreak {
    display: block;
}


@media screen and (min-width: 768px) {
    .footerBreak {
        display: none;
    }
}

@media screen and (min-width: 1600px) {


    .footerNav {
        text-align: center;
        width: 50%;
        margin: 0 auto;
    }
    
    .footerItem {
        margin: 0 0.5rem;
        font-weight: 600;
        font-size: 1.25rem;
    }


}
#landingPage {
    background-color: var(--reb-mute);
    text-align: center;
    border-radius: 1rem;
    margin: 1rem auto;
    padding: 1rem; 
    box-shadow: 1px 3px 19px 1px rgba(0,0,0,0.92);
}

#homeImage {
    max-width: 100%;
}

#landingPage > h3, #landingPage > p {
    font-family: 'Open Sans';
    color: black;
}


@media screen and (max-width: 600px) {
    #landingPage {
        padding: 1rem 1rem 4rem 1rem;
        margin: 1rem 0.5rem 5rem 0.5rem;
    }
    
}



@media screen and (min-width: 800px) {
    #landingPage {
        max-width: 1000px;
        
    }
}


#pricePage {
    padding: 1rem;
    border-radius: 1rem;
    margin: 1rem 0.5rem 5rem 0.5rem;
    border: 5px solid brown;
    background-image: url(/static/media/cb.2a8d4ad1.jpg);
}

.priceGrid {

    display: grid;
    text-align: center;
    grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
    grid-template-columns: 1fr;
    grid-gap: 0.5rem;
    gap: 0.5rem;

    padding-bottom: 5rem;
    grid-template-areas: "a"
                        "a1"
                        "a2"
                         "b"
                         "c"
                         "d"
                         "e";
}

.males1 {
    grid-area: a;
}

.males2 {
    grid-area: a1;
}

.males3 {
    grid-area: a2;
}

.females {
    grid-area: b;
}

.softshells {
    grid-area: c;
}

.fish {
    grid-area: d;
}

.other {
    grid-area: e;
}

#priceTitle {
    font-size: 2rem;
    margin: 0 auto;
    text-align:center;
}

h3, p {
    font-family: 'Fredericka the Great', cursive;
    color: white;
}

@media screen and (min-width: 768px) {
    #pricePage {
        max-width: 75%;
        margin: 1rem auto;
    }
    .priceGrid {


        text-align: left;
        display: grid;
        grid-template-rows: 1fr 1fr 1fr;
        grid-template-columns: 1fr 1fr 1fr 1fr 2fr;
        grid-gap: 0.5rem;
        gap: 0.5rem;
        grid-template-areas: "a a1 a2 b b1"
                             "c c1 c1 d d1"
                             "e e e e e";
    }
    .males1 {
        grid-area: a;
    }

    .males2 {
        grid-area: a1;
    }

    .males3 {
        grid-area: a2;
    }
    
    .females {
        grid-area: b1;
    }
    
    .softshells {
        grid-area: c1;
    }
    
    .fish {
        grid-area: d1;
    }
    
    .other {
        grid-area: e;
    }
}
#contactPage {
    background-color: var(--reb-mute);
    text-align: center;
    border-radius: 1rem;
    margin: 1rem 0.5rem 6rem 0.5rem;
    padding: 1rem; 
    box-shadow: 1px 3px 19px 1px rgba(0,0,0,0.92);
}

 

#contactPage > h3, #contactPage > p {
    font-family: 'Open Sans';
    color: black;
}




@media screen and (min-width: 800px) {
    #contactPage {
        max-width: 1000px;
        margin: 1rem auto;
    }

    #contactForm {
        padding-bottom:5rem;
    }
    
    
}
#picsPage {
    background-color: var(--reb-mute);
    text-align: center;
    border-radius: 1rem;
    margin: 1rem auto 5rem auto;
    padding: 1rem 1rem 6rem 1rem; 
    box-shadow: 1px 3px 19px 1px rgba(0,0,0,0.92);
}

 

#picsPage > h3, #picsPage > p {
    font-family: 'Open Sans';
    color: black;
}


.picsImg {
    max-width: 100%;
    max-height: 480px;
}

.picsButton {
    margin: 0.5rem;
    font-size: 1.75rem;
    border-radius: 10px;
    padding: 0.5rem;
}

@media screen and (min-width: 800px) {
    #picsPage {
        max-width: 1000px;
        
    }
}
:root {
    --reb: rgb(240, 43, 73);
    --reb-mute: rgb(255, 138, 144);
    --blu: rgb(43, 240, 181);
    --blu-mute: rgb(43, 240, 181, 0.5);
}

* {
    font-family: "IBM Plex Sans";
}

body {
    background-image: url(/static/media/IMG_0459.66c1aa15.jpg);
    margin: 0 !important;
}


